"use client";

import React, { useState, useEffect, useMemo } from 'react';
import { PieChart, Pie, Cell, ResponsiveContainer } from 'recharts';

// --- Types ---
interface GaugeProps {
  targetValue?: number; // The peak value to reach (0-100)
  speed?: number;       // Animation speed multiplier
}

// --- Data for the Gauge Track ---
const GAUGE_DATA = [
  { name: 'Track', value: 100 }, 
];

// --- Custom Needle Component (The Arrow) ---
const renderNeedle = (value: number, cx: number, cy: number, iR: number, oR: number, color: string) => {
  const total = 100; // Based on our data scale
  const ang = 180.0 * (1 - value / total); // Angle in degrees
  const length = (iR + 2 * oR) / 3; // Length of the needle
  const sin = Math.sin(-Math.PI * ang / 180);
  const cos = Math.cos(-Math.PI * ang / 180);
  
  const r = 5; // Radius of the center dot
  const x0 = cx;
  const y0 = cy;
  const xba = x0 + r * sin;
  const yba = y0 - r * cos;
  const xbb = x0 - r * sin;
  const ybb = y0 + r * cos;
  const xp = x0 + length * cos;
  const yp = y0 + length * sin;

  return (
    <g key="needle-group">
      <circle cx={x0} cy={y0} r={r} fill={color} stroke="none" />
      <path 
        d={`M${xba} ${yba}L${xbb} ${ybb}L${xp} ${yp}Z`} 
        fill={color} 
        stroke="none" 
      />
    </g>
  );
};

export default function AnimatedArrowChart({ targetValue = 80, speed = 0.5 }: GaugeProps) {
  const [value, setValue] = useState(0);
  const [direction, setDirection] = useState(1); // 1 for up, -1 for down

  useEffect(() => {
    let animationFrameId: number;

    const animate = () => {
      setValue((prev) => {
        let nextValue = prev + speed * direction;

        // Check boundaries and flip direction
        if (nextValue >= targetValue) {
          nextValue = targetValue;
          setDirection(-1);
        } else if (nextValue <= 0) {
          nextValue = 0;
          setDirection(1);
        }

        return nextValue;
      });

      animationFrameId = requestAnimationFrame(animate);
    };

    animationFrameId = requestAnimationFrame(animate);
    return () => cancelAnimationFrame(animationFrameId);
  }, [direction, targetValue, speed]);

  return (
    <div className="flex flex-col items-center justify-center w-full h-[400px] bg-slate-900 rounded-xl p-4">
      <h2 className="text-white text-xl font-bold mb-4">Live Progress Meter</h2>
      
      <div className="w-full h-full relative">
        <ResponsiveContainer width="100%" height="100%">
          <PieChart>
            <Pie
              dataKey="value"
              startAngle={180}
              endAngle={0}
              data={GAUGE_DATA}
              cx="50%"
              cy="70%"
              innerRadius={80}
              outerRadius={120}
              fill="#334155" // Background track color
              stroke="none"
              isAnimationActive={false} // Disable default animation for needle smoothness
            >
              <Cell fill="#1e293b" />
            </Pie>
            
            {/* Overlay the Needle */}
            {/* The renderNeedle logic needs CX/CY which Recharts calculates internally. 
                In a production app, these are usually passed via custom shape props, 
                but for standard use, we can estimate based on container size. */}
            <svg>
                {/* Passing static cx/cy for demonstration. 
                  In ResponsiveContainer, we center it.
                */}
                {renderNeedle(value, 200, 200, 80, 120, "#22c55e")}
            </svg>
          </PieChart>
        </ResponsiveContainer>
        
        {/* Dynamic Text Value */}
        <div 
          className="absolute left-1/2 -translate-x-1/2 bottom-[25%] text-4xl font-mono font-bold"
          style={{ color: '#22c55e' }}
        >
          {Math.round(value)}%
        </div>
      </div>
    </div>
  );
}